import React, { Suspense } from "react";

import './Relationships.scss';
import detect from "detect.js";
//import Html from '../../../../Shared/images/svg/HTML/Html';
//import Css from '../../../../Shared/images/svg/CSS/Css';
//import Js from '../../../../Shared/images/svg/JS/Js';
const Html = React.lazy( () => import('../../../../Shared/images/svg/HTML/Html') );
const Css = React.lazy( () => import('../../../../Shared/images/svg/CSS/Css') );
const Js = React.lazy( () => import('../../../../Shared/images/svg/JS/Js') );

export default class Relationships extends React.Component {
    constructor(props) {
        super(props);

        const userAgent = detect.parse(navigator.userAgent);
        this.isNeedToFix = /Windows|Android/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);
    }

    render() {
        const contentText = this.props.contentText;

        return (
            <section className="relationships">
                <div className="relationships__container">
                    <h2 className={`relationships__title heading2${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>
                        {contentText.title}
                    </h2>
                    <div className="relationships__list">
                        <div className="relationships__list-item">
                            <div className="relationships__list-item-img">
                                <Suspense fallback=''><Html /></Suspense>
                            </div>
                            <div className="relationships__list-item-info">
                                <h3 className={`relationships__list-item-info-title heading3${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>
                                    {contentText.html.title}
                                </h3>
                                <p className="relationships__list-item-info-description paragraph2">
                                    {contentText.html.description}
                                </p>
                            </div>
                        </div>
                        <div className="relationships__list-item">
                            <div className="relationships__list-item-img">
                                <Suspense fallback=''><Css /></Suspense>
                            </div>
                            <div className="relationships__list-item-info">
                                <h3 className={`relationships__list-item-info-title heading3${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>
                                    {contentText.css.title}
                                </h3>
                                <p className="relationships__list-item-info-description paragraph2">
                                    {contentText.css.description}
                                </p>
                            </div>
                        </div>
                        <div className="relationships__list-item">
                            <div className="relationships__list-item-img">
                                <Suspense fallback=''><Js /></Suspense>
                            </div>
                            <div className="relationships__list-item-info info-js-fix">
                                <h3 className={`relationships__list-item-info-title heading3${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>
                                    {contentText.js.title}
                                </h3>
                                <p className="relationships__list-item-info-description paragraph2">
                                    {contentText.js.description}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
